svg.icon.icon-left{
    margin:0 10px;
}
.todoFolderItem{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align:center;
        align-items:center;
    padding:10px 0;
    cursor: pointer;
}
.todoFolderItem.active{
    background:rgba(190, 209, 232,.6);

}
.todoFolderItem svg.noModify{
    display: none; 
}
.todoFolderItem .todo-icon-modify{
    display: none;
}
.todoFolderItem .todo-icon-modify.active{
    display: inline-block;
    fill:#999;
    width:20px;
    height:20px;
}
.todoFolderItem .todo-icon-modify.active:hover{
    fill:#000;
}
.todoFolderItem .todoFolderName{
    -ms-flex:1 1;
        flex:1 1;

}



