.UserDialog-Wrapper{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align: start;
        align-items: flex-start;
    z-index:3;
    background:transparent url('https://i.loli.net/2017/10/15/59e2d730ac9a0.jpeg ') no-repeat center;
    background-size:cover;
}
.UserDialog{
    margin-top:100px;
    border-radius:4px;
    min-width:20em;
    background:white;
    border:1px solid #aaa;
    -webkit-box-shadow:0 0 1em hsla(0,0%,0%,0.35);
            box-shadow:0 0 1em hsla(0,0%,0%,0.35);
    padding-top:30px;
    padding-bottom:10px;
}
.UserDialog >.logo{
   font-size:2em;
    text-align: center;
    margin:.67em 0;
}



